import React,{useState,useReducer,useEffect} from 'react';
import ClassTable from './ClassTable';
import ClassExecutor from './ClassExecutor';
import ClassFilter from './ClassFilter';
import {DebugContext} from './Context.js';
import './App.css';
import Particles from 'react-particles-js';
import particlesOptions from './particles.js';

import {useClubType, useClassType} from './HttpRequest';

function App() {

  const BOOK_OPEN_IN_SECOND = 46*60*60;
  const AFTER_OPEN_IN_SECOND = 4*24*60*60;
  const BOOK_OFFSET_IN_SECOND = 1; // FF is 0.5 seconds behind us
  const BOOK_DURATION_IN_SECOND = 10;
  const BOOK_WAIT_IN_MS = 150;
  const DEBUG_BOOKING = false;

  let [accessToken, setAccessToken] = useState(_=>localStorage.getItem('accessToken')||'');
  let [country, setCountry] = useState(_=>localStorage.getItem('country')||'sg')

  let [targetClass, setTargetClass] = useReducer((curr,c)=>{
    if(curr?.classId !== c?.classId){
      //console.log(c);
      return c;
    }else{
      return null;
    }
  }, null);

  let clubTypes = useClubType({accessToken,country});
  let classTypes = useClassType({accessToken,country});

  let [paramClub, setParamClub] = useState(null);
  let [paramClasses, setParamClasses] = useState(null);
  let [paramTime, setParamTime] = useState(null);

  function deselect(e){
    if(e.target === e.currentTarget){
      setTargetClass(null);
    }
  }

  useEffect(()=>{
    Notification.requestPermission().then(()=>{
      let hiddenState = false;
      let currentNoti = null;
      setInterval(()=>{
        if(document.hidden){
          if(!hiddenState){
            hiddenState = true;
            let noti = new Notification('The FF Booker tab is not active!', {
              body: 'Click here to return to the tab',
              requireInteraction: true
            });
            noti.onclick = () => {window.focus(); window.parent.focus(); noti.close();};
            currentNoti = noti;
          }
        }else{
          hiddenState = false;
          if(currentNoti) currentNoti.close();
        }
      }, 100);
      window.onbeforeunload = () => {if(currentNoti) currentNoti.close();};
    });
  }, []);

  return (
      <DebugContext.Provider value={DEBUG_BOOKING}>
        <div style={{...backgroundStyle, display:'flex'}} onClick={deselect}>
          <div style={{...blockStyle, maxWidth:1000}} onClick={deselect}>
            <ClassTable handleSelection={c => setTargetClass(c)}
              fromDateAdj={BOOK_OPEN_IN_SECOND}
              toDateAdj={BOOK_OPEN_IN_SECOND+AFTER_OPEN_IN_SECOND}
              selected={targetClass}
              accessToken={accessToken}
              country={country}
              {...{paramClub,paramClasses,paramTime}}
              {...{clubTypes,classTypes}}
            />
          </div>
          <div style={{...blockStyle, minWidth:'325px', maxWidth:'400px', flexGrow: 0}} onClick={deselect}>
            <div style={{display:'flex', flexDirection:'column', maxHeight:'100%'}}>
              <ClassFilter {...{setParamClub,setParamClasses,setParamTime}}
                           {...{accessToken, setAccessToken}}
                           {...{country}}
                           {...{clubTypes,classTypes}}
              />
              <ClassExecutor handleSelection={c => setTargetClass(c)}
                targetClass={targetClass}
                bookOpen={BOOK_OPEN_IN_SECOND}
                bookOffset={BOOK_OFFSET_IN_SECOND}
                bookDuration={BOOK_DURATION_IN_SECOND}
                bookWait={BOOK_WAIT_IN_MS}
                accessToken={accessToken}
                country={country}
                {...{clubTypes,classTypes}}
              />
            </div>
          </div>
        </div>
        <Particles width='100vw' height='100vh' params={particlesOptions}/>
      </DebugContext.Provider>
  );
}

export default App;

var backgroundStyle = {
  position: 'absolute',
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
  padding: '0 5px',
};

var blockStyle = {
  margin: '5px 5px',
  flexGrow: 1
}