import React,{useState} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import DialogActions from '@material-ui/core/DialogActions';

function ClassSetting(props){

  let [tokenText, setTokenText] = useState(props.accessToken);
  let [countryText, setCountryText] = useState(props.country);

  let handleSave = _ => {
    props.setAccessToken(tokenText);
    localStorage.setItem('accessToken', tokenText);
    if(props.country !== countryText){
      localStorage.setItem('country', countryText);
      localStorage.setItem('watches', '{}');
      localStorage.setItem('favClasses', '{}');
      localStorage.setItem('favClubs', '{}');
      window.location.reload();
    }
    props.setOpen(false);
  };

  let handleClose = _ => {
    setTokenText(props.accessToken);
    setCountryText(props.country);
    props.setOpen(false);
  };

  return (
    <Dialog open={props.open} onClose={handleClose}>
      <DialogTitle>Settings</DialogTitle>
      <DialogContent>
        <DialogContentText>Access Token is required for getting class capacity and booking classes.</DialogContentText>
        <TextField label="Access Token" placeholder="Bearer 58f7GciOiJIUzI1NiJ9.eyJpc3MiOiJodHRw3..."
        multiline fullWidth rows={8} variant="outlined" InputLabelProps={{shrink: true}}
        value={tokenText} onChange={e=>setTokenText(e.target.value)}/>
        <br/>
        <br/>
        <DialogContentText>Choose your country:</DialogContentText>
        <FormControl variant="outlined">
          <InputLabel htmlFor='country-select'>Country</InputLabel>
          <Select label='Country' value={countryText} variant="outlined" onChange={e=>setCountryText(e.target.value)}
          notched inputProps={{id:'country-select'}}>
            <MenuItem value='sg'>Singapore</MenuItem>
            <MenuItem value='th'>Thailand</MenuItem>
          </Select>
          {props.country!==countryText && <FormHelperText>Execution Queue and Favorites will be reset</FormHelperText>}
        </FormControl>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button onClick={handleSave} color="primary">
          Save
        </Button>
      </DialogActions>
    </Dialog>
  );
}

export default ClassSetting;
